<template>
    <div class="com-footer">
        <ul>
            <li><router-link :to="{ path: '/home' }"></router-link></li>
            <li><router-link :to="{ path: '/licai' }"></router-link></li>
            <li><router-link :to="{ path: '/personal' }"></router-link></li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: '',
        components: {},
        data () {
            return {}
        }
    }
</script>

<style lang="scss" rel="stylesheet/scss">
    .com-footer{
        height: 40px;
        line-height: 40px;
        background: #fff;
        border-top: 1px solid #e0e0e0;
        /*<!--box-shadow: 0px -1px 1px #e0e0e0;-->*/
        ul{
            display: flex;
            /*border-top: 1px solid #ccc;*/
            li{
                flex: 1;
                text-align: center;
                a{
                    text-decoration: none;
                    color: #5e5e5e;
                    display: block;
                    /*font-size: .22rem;*/
                    &.router-link-active{
                        color: #f44e50 !important;
                    }
                }
            }
        }
    }
</style>



