<template>
    <div>
        <div class="route-fade-in"  ref="comBodyLicai">
            <div>
                <!-- -->
                <v-swiper :opts="swiperOpts" :dataVal="imgs"></v-swiper>
                <div class="types">
                    <div class="type01">
                        <div class="type-circle"></div>
                        <div class="type-title"></div>
                    </div>
                    <div class="type02">
                        <div class="type-circle"></div>
                        <div class="type-title"></div>
                    </div>
                    <div class="type03">
                        <div class="type-circle"></div>
                        <div class="type-title"></div>
                    </div>
                </div>
                <!---->
                <div class="jj">
                    <div class="jj-title">-<span></span></div>
                </div>
                <ul class="jj-ul">
                    <li class="jj-li" v-for="(item,index) in listData" @click="clickLi">
                        <div class="jj-li-left">
                            <p class="jj-num">{{item.num}}%</p>
                            <p>{{item.numDes}}|</p>
                        </div>
                        <div class="jj-li-right">
                            <p class="jj-title">{{item.name}}<span class="tag"></span></p>
                            <p>{{item.nameDes}}</p>
                        </div>
                    </li>
                </ul>
                <!---->
                <div class="jj">
                    <div class="jj-title">-<span></span></div>
                </div>
                <ul class="jj-ul">
                    <li class="jj-li" v-for="(item,index) in listData" @click="clickLi">
                        <div class="jj-li-left">
                            <p class="jj-num">{{item.num}}%</p>
                            <p>{{item.numDes}}|</p>
                        </div>
                        <div class="jj-li-right">
                            <p class="jj-title">{{item.name}}</p>
                            <p>{{item.nameDes}}</p>
                        </div>
                    </li>
                </ul>
                <div class="z-more z-more-bottom"></div>
            </div>
        </div>
    </div>
</template>

<script>
    //    better scroller
    import BScroll from 'better-scroll';
    import VSwiper from '../components/swiper/ComSwiper'
    export default {
        name: '',
        components: {
            VSwiper
        },
        data () {
            return {
                LicaiScroll:'',
                listData:[
                    {
                        "id":"1",
                        "num":"4.3",
                        "numDes":"",
                        "name":"",
                        "nameDes":" "
                    },
                    {
                        "id":"2",
                        "num":"4.3",
                        "numDes":"",
                        "name":"",
                        "nameDes":" "
                    },
                    {
                        "id":"3",
                        "num":"4.3",
                        "numDes":"",
                        "name":"",
                        "nameDes":" "
                    }
                ],
                swiperOpts:{
                    pagination: '.swiper-pagination',
//                    nextButton: '.swiper-button-next',
//                    prevButton: '.swiper-button-prev',
                    slidesPerView: 1,
                    speed:800,
                    paginationClickable: true,
                    loop: true,
                    autoplay: 4000,
                },
                imgs:[
                    {
                        "imgSrc":"../../static/imgs/banner01.jpg",
                        "href":"http://www.sina.com.cn/",
                    },
                    {
                        "imgSrc":"../../static/imgs/banner01.jpg",
                        "href":"http://www.sina.com.cn/",
                    },
                    {
                        "imgSrc":"../../static/imgs/banner01.jpg",
                        "href":"http://www.sina.com.cn/",
                    },
                    {
                        "imgSrc":"../../static/imgs/banner01.jpg",
                        "href":"http://www.sina.com.cn/",
                    }
                ],
            }
        },
        created(){
            this.$nextTick(function(){

                this.$refs.comBodyLicai.style.height=this.$parent.$el.firstChild.offsetHeight+'px'
                setTimeout(()=>{
                    this._LicaiScroll()
                },10)


            })

        },
        methods:{
            _LicaiScroll() {
                this.LicaiScroll = new BScroll(this.$refs.comBodyLicai,{
                    click: true,
                    deceleration: 0.005,
                });
            },
            lookMore:function () {
                this.$router.push({
                    path:'/home/jijinlist',
                })
            },
            clickLi:function () {

                this.$router.push({
                    path:'/home/licaidetail',
//                    query:{listId:this.listData[i].id}
                })
            },
        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
    .types{
        display: flex;
        justify-content: space-between;
        padding: 1rem;
        .type01,.type02,.type03{
            width: 1.4rem;
            height: 1.4rem;
            font-size: .26rem;
            color: #666;
            .type-circle{
                width: .8rem;
                height: .8rem;
                margin: 0 auto;
                border-radius: .4rem;
            }
        }
        .type01{
            .type-circle{
                background: #f8b551;
            }
        }
        .type02{
            .type-circle{
                background: #d2c19b;
            }
        }
        .type03{
            .type-circle{
                background: #cbcbcb;
            }
        }
        .type-title{
            text-align: center;
            margin-top: .15rem;
        }
    }

    .jj{
        padding: 0 .2rem;
        overflow: hidden;
        margin-bottom: .4rem;
        .jj-title{
            font-size: .28rem;
            color: #333;
            span{
                font-size: .24rem;
                color: #f8b551;
            }
        }
    }
    .jj-ul{
        .jj-li{
            overflow: hidden;
            padding: 0 0.9rem;
            margin-bottom: 0.8rem;
            font-size: .26rem;
            color: #666;
            .jj-li-left{
                float: left;
                .jj-num{
                    color: #f44e51;
                    font-size: 0.6rem;
                    height: 0.8rem;
                }
            }
            .jj-li-right{
                .jj-title{
                    color: #333;
                    font-size: 0.4rem;
                    height: 0.8rem;
                    padding-top: .1rem;
                    .tag{
                        display: inline-block;
                        font-size: .24rem;
                        color: #ff8383;
                        background: #ffe8e8;
                        position: relative;
                        top:-.05rem;
                        margin-left: .05rem;
                        padding: .05rem;
                    }
                }
                float: right;
            }
        }
    }
    .z-more{
        color: #5087c0;
        text-align: center;
        margin-bottom: .6rem;
        font-size: .28rem;
    }
    .z-more-bottom{
        padding-bottom: 1.4rem;
    }
</style>



