<template>
    <div class="main-wrap">
        <v-echart :chartOption="chartOption"></v-echart>
        <v-market :marketOption="marketOption"></v-market>
    </div>
</template>
<script>
    import VMarket from '../components/JiDetail/Market'
    import VEchart from '../components/JiDetail/echart'

    const themeColor = ['#f14d4c', '#f9b552', '#d3c19b', '#e0e0e0', '#91c7ae','#749f83',  '#ca8622', '#bda29a','#6e7074', '#546570', '#c4ccd3'];
    const chartData = [
        {name:"",value:10},
        {name:"",value:30},
        {name:"",value:40},
        {name:"",value:20}
    ];

    export default {
        components: {
            VEchart,
            VMarket
        },
        data() {
            return {
                marketOption:{
                    style: {
                        width:'100%',
                        height:'6rem'
                    },
                    symbol:"100032"
                },
                chartOption : {
                    option:{
                        color: themeColor,
                        legend: null,
                        series: [
                            {
                                name:'',
                                type:'pie',
                                center: ['50%', '40%'],
                                radius: ['35%', '60%'],
                                avoidLabelOverlap: false,
                                label: null,
                                silent: true,
                                clockwise: false,
                                labelLine: {
                                    normal: {
                                        show: false
                                    }
                                },
                                data: chartData
                            }
                        ]
                    }
                }
            }
        },
        created() {
            document.body.style.backgroundColor = "#fff";
            for(var key in chartData){
                chartData[key].color = themeColor[key];
            }
            this.chartOption.chartData = chartData;
        },
        mounted:function () {

        },
        methods: {

        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
    .main-wrap{
        width: 100%;
        max-width: 750px;
        margin:0 auto;
        font-size: .16rem;
    }
</style>



