<template>
    <div class="route-fade-in"  ref="comBodyJijin">
        <div>
            <div class="jj-tags">
                <div class="tag-bg01">10</div>
                <div class="tag-bg02"></div>
                <div class="tag-bg03"></div>
            </div>
            <!---->
            <div class="jj">
                <div class="jj-title">-<span></span></div>
            </div>
            <ul class="jj-ul">
                <li class="jj-li" v-for="(item,index) in listData"  @click="clickLi">
                    <div class="jj-li-left">
                        <p class="jj-num">{{item.num}}%</p>
                        <p>{{item.numDes}}|</p>
                    </div>
                    <div class="jj-li-right">
                        <p class="jj-title">{{item.name}}</p>
                        <p>{{item.nameDes}}</p>
                    </div>
                </li>
            </ul>
            <div class="z-more" @click="lookMore"></div>

            <!---->
            <div class="jj">
                <div class="jj-title">-<span></span></div>
            </div>
            <ul class="jj-ul">
                <li class="jj-li" v-for="(item,index) in listData"  @click="clickLi">
                    <div class="jj-li-left">
                        <p class="jj-num">{{item.num}}%</p>
                        <p>{{item.numDes}}|</p>
                    </div>
                    <div class="jj-li-right">
                        <p class="jj-title">{{item.name}}</p>
                        <p>{{item.nameDes}}</p>
                    </div>
                </li>
            </ul>
            <div class="z-more" @click="lookMore"></div>

            <!---->
            <div class="jj">
                <div class="jj-title">-<span></span></div>
            </div>
            <ul class="jj-ul">
                <li class="jj-li" v-for="(item,index) in listData"  @click="clickLi">
                    <div class="jj-li-left">
                        <p class="jj-num">{{item.num}}%</p>
                        <p>{{item.numDes}}|</p>
                    </div>
                    <div class="jj-li-right">
                        <p class="jj-title">{{item.name}}</p>
                        <p>{{item.nameDes}}</p>
                    </div>
                </li>
            </ul>
            <div class="z-more z-more-bottom" @click="lookMore"></div>
        </div>
    </div>
</template>

<script>
    //    better scroller
    import BScroll from 'better-scroll';
    export default {
        name: '',
        components: {},
        props:{
            opts:''
        },
        data () {
            return {
                JijinScroll:'',
                listData:[
                {
                    "id":"1",
                    "num":"5.85",
                    "numDes":"",
                    "name":"",
                    "nameDes":""
                },
                {
                    "id":"2",
                    "num":"5.85",
                    "numDes":"",
                    "name":"",
                    "nameDes":""
                },
                {
                    "id":"3",
                    "num":"5.85",
                    "numDes":"",
                    "name":"",
                    "nameDes":""
                }
                ]
            }
        },
        created(){
            this.$nextTick(function(){

                this.$refs.comBodyJijin.style.height=this.$parent.$el.firstChild.offsetHeight+'px'
                setTimeout(()=>{
                    this._JijinScroll()
                },10)


            })

        },
        methods:{
            _JijinScroll() {
                this.JijinScroll = new BScroll(this.$refs.comBodyJijin,{
                    click: true,
                    deceleration: 0.005,
                });
            },
            lookMore:function () {
                this.$router.push({
                    path:'/home/jijinlist',
                })
            },
            clickLi:function (i) {

                this.$router.push({
                    path:'/home/detail',
//                    query:{listId:this.listData[i].id}
                })
            },
        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
    .jj{
        padding: 0 .2rem;
        overflow: hidden;
        margin-bottom: .4rem;
        .jj-title{
            font-size: .28rem;
            color: #333;
            span{
                font-size: .24rem;
                color: #f8b551;
            }
        }
    }
    .jj-ul{
        .jj-li{
            overflow: hidden;
            padding: 0 0.9rem;
            margin-bottom: 0.8rem;
            font-size: .26rem;
            color: #666;
            .jj-li-left{
                float: left;
                .jj-num{
                    color: #f44e51;
                    font-size: 0.6rem;
                    height: 0.8rem;
                }
            }
            .jj-li-right{
                .jj-title{
                    color: #333;
                    font-size: 0.4rem;
                    height: 0.8rem;
                    padding-top: .1rem;
                }
                float: right;
            }
        }
    }
    .z-more{
        color: #5087c0;
        text-align: center;
        margin-bottom: .6rem;
        font-size: .28rem;
    }
    .z-more-bottom{
        padding-bottom: 1.4rem;
    }
    .jj-tags{
        display: flex;
        justify-content: space-between;
        padding: 0 .2rem;
        margin: .2rem 0 .4rem;
        div{
            width: 2.3rem;
            height: 0.7rem;
            line-height: 0.7rem;
            font-size: 0.28rem;
            text-align: center;
            color: #fff;
            &.tag-bg01{
                background: #f8b551;
            }
            &.tag-bg02{
                background: #d2c19b;
            }
            &.tag-bg03{
                background: #cbcbcb;
            }
        }
    }
</style>



