<template>
  <div class="echartWrap" v-if="rendered">
    <div style="width:100%;height: 500px;" ref="chartWrap"></div>
    <div class="info-all"> <span>5000.00</span></div>
    <div class="info-list">
      <div class="item" v-for="(item,index) in chartOption.chartData">
        <i :style="{backgroundColor:item.color}"></i>
        <span class="name">{{item.name}}</span>
        <span>{{setVal[index]}}</span>
      </div>
    </div>
  </div>
</template>

<script>
    import ECharts from 'echarts/dist/echarts.simple.min'

    export default {
        data () {
            return {
                rendered:false,
                newVlaue: []
            }
        },
        props: {
            chartOption: ""
        },
        computed:{

            setVal:function () {
                for(var key in this.chartOption.chartData){
                    this.newVlaue[key] = this.chartOption.chartData[key].value.toFixed(2);
                }
                return this.newVlaue;
            }
        },
        mounted:function () {
            let chart = ECharts.init(this.$refs.chartWrap);

            chart.setOption(this.chartOption.option);

            window.addEventListener('resize', function () {
                chart.resize();
            });
        },
        created() {
            this.rendered = true;
        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .echartWrap{
    position: relative;
    width: 100%;
    background-color: #f5f5f5;

    .info-all{
      display: block;
      position: absolute;
      top:32.5%;
      left: 0;
      width: 100%;
      text-align: center;
      pointer-events: none;
      color: #999;
      font-size: .3rem;
      line-height: .38rem;
      span{
        display: block;
        color: #f14d4c;
      }
    }
    .info-list{
      position: absolute;
      left: 0;
      width: 100%;
      bottom: 5%;
      padding-left: 10%;
      &:before,
      &:after {
        content:"";
        display: table;
      }
      &:after {
        clear:both;
        overflow: hidden;
      }
      .item{
        float: left;
        width: 50%;
        line-height: .4rem;
        padding:.1rem;
        font-size: .3rem;

        i{
          display: inline-block;
          width: .2rem;
          height:.2rem;
          vertical-align: top;
          margin-top: .08rem;
          margin-right: .1rem;
          border-radius: .04rem;
        }
        span{
          color: #999;
          font-size: .26rem;
        }
      }
    }
  }

</style>



