<template>
    <div class="flow-realname" ref="flowRealName">
        <div>
            <header>
                <a class="arrow-back" @click="backTo"></a>
                <span></span>
            </header>
            <div class="verify">
                <div class="f-default"><img src="../../assets/f_default.png"></div>
                <h3></h3>
                <p class="subtit"></p>
                <ul class="form top-margin">
                    <li>
                        <label for="u_name">&emsp;&emsp;</label>
                        <input type="text" id="u_name" placeholder="">
                    </li>
                    <li>
                        <label for="u_idnum"></label>
                        <input type="text" id="u_idnum" placeholder="">
                    </li>
                </ul>
                <ul class="form">
                    <li>
                        <label for="u_banknum"></label>
                        <input type="text" id="u_banknum" placeholder="">
                    </li>
                    <li>
                        <label></label>
                        <select class="bank">
                            <option value="1">&nbsp;</option>
                            <option value="2">&nbsp;</option>
                            <option value="3"></option>
                        </select>
                    </li>
                    <li>
                        <label for="u_phone">&ensp;&ensp;</label>
                        <input type="text" id="u_phone" placeholder="">
                    </li>
                </ul>
                <div class="agreement">
                    <div class="checkbox">
                        <input type="checkbox" id="agree" checked>
                        <label for="agree"></label>
                    </div>
                    <p><a href="javascript:;"></a></p>
                </div>
                <div class="btn" @click="VerCode"></div>
                <div class="h50"></div>
            </div>
        </div>
    </div>
</template>

<script>
    //    better scroller
    import BScroll from 'better-scroll';
    export default {
        name: '',
        components: {},
        data () {
            return {
                RealNameScroll:''
            }
        },
        created(){
            //$nextTickdom
            this.$nextTick(() => {
                setTimeout(()=>{
                    this._RealNameScroll();
                },300)
            })
        },
        methods:{
            _RealNameScroll:function () {
                this.RealNameScroll = new BScroll(this.$refs.flowRealName,{
                    click: true,
                    deceleration: 0.005,
                });
            },
            backTo(){
                this.$router.go(-1)
            },
            VerCode(){
                //
                this.$router.push(
                    {
                        path:'/flow/vercode',
                    }
                )
            }
        }
    }
</script>

<style lang="scss" rel="stylesheet/scss">
    .flow-realname{
        background: #f5f5f5;
        header{
            /*position: fixed;*/
            /*top: 0;*/
            position: relative;
            width: 100%;
            height: .9rem;
            font-size: .32rem;
            line-height: .9rem;
            text-align: center;
            color: #666;
            background-color: #fff;
            box-shadow: 0 .02rem .02rem 0 #e9ebed;
        }
        .arrow-back{
            position: absolute;
            margin: auto;
            left: .28rem;
            top: 0;
            bottom: 0;
            width: .3rem;
            height: .3rem;
            transform: rotate(45deg);
            border-left: 1px solid #cecece;
            border-bottom: 1px solid #cecece;
        }
        .r-href{
            position: absolute;
            right: .2rem;
            font-size: .26rem;
            color: #5781bd;
            text-decoration: none;
        }
        .verify .f-default{
            padding: .9rem 0 .16rem;
            font-size: 0;
            text-align: center;
        }
        .verify h3{
            font-size: .32rem;
            line-height: 1.5;
            text-align: center;
            color: #f8b551;
        }
        .verify .subtit{
            padding-top: .08rem;
            font-size: .26rem;
            line-height: 1.5;
            text-align: center;
            color: #999;
        }
        .verify .top-margin{
            margin-top: .94rem;
        }
        .form-tpd{
            padding-top: .82rem;
        }
        .form{
            margin-top: .2rem;
            background-color: #fff;
        }
        .form li{
            display: flex;
            margin-left: .2rem;
            height: .9rem;
            border-bottom: 1px solid #eee;
            overflow: hidden;
        }
        .form li:last-of-type{
            border-bottom: none;
        }
        .form .key{
            font-size: .26rem;
            line-height: .84rem;
            color: #666;
        }
        .form .value{
            flex-grow: 1;
            margin-right: .2rem;
            font-size: .26rem;
            line-height: .84rem;
            text-align: right;
            color: #f8b551;
        }
        .form label{
            align-self: center;
            flex-shrink: 0;
            margin-right: .38rem;
            padding-right: .4rem;
            height: .3rem;
            border-right: 1px solid #eee;
            font-size: .26rem;
            line-height: .3rem;
            color: #666;
        }
        .form input{
            flex-grow: 1;
            flex-shrink: 1;
            margin-right: .2rem;
            min-width: 2.2rem;
            border: none;
            outline: none;
            font-size: .26rem;
        }
        .form input::-webkit-input-placeholder{
            color: #ccc;
        }
        .form .f-btn{
            width: 2rem;
            font-size: .26rem;
            line-height: .9rem;
            text-align: center;
            color: #fff;
            background-color: #5781bd;
        }
        .form .bank{
            flex-grow: 1;
            padding: 0 .1rem;
            margin-left: -.1rem;
            margin-right: .2rem;
            border: none;
            outline: none;
            font-size: .26rem;
            line-height: .9rem;
            text-overflow: ellipsis;
            color: #666;
            -webkit-appearance: none;
            -moz-appearance: none;
        }
        .agreement{
            position: relative;
            padding-top: .16rem;
            padding-left: .54rem;
            font-size: 0;
        }
        .agreement p{
            font-size: .26rem;
            line-height: 1.5;
            color: #666;
        }
        .agreement p a{
            color: #ccc;
            text-decoration: none;
        }
        .agreement a{
            font-size: .26rem;
            line-height: 1.5;
            color: #ccc;
            text-decoration: none;
        }
        .checkbox{
            position: absolute;
            left: .18rem;
            top: .22rem;
            width: .3rem;
            height: .3rem;
        }
        .checkbox input[type=checkbox]{
            display: none;
        }
        .checkbox input[type=checkbox]~label{
            display: inline-block;
            width: .3rem;
            height: .3rem;
            font-size: .26rem;
            line-height: 1.5;
            background: url(../../assets/check.png) no-repeat center/contain;
        }
        .checkbox input[type=checkbox]:checked~label{
            background: url(../../assets/checked.png) no-repeat center/contain;
        }
        .btn{
            margin-top: .4rem;
            margin-left: .2rem;
            margin-right: .2rem;
            height: .9rem;
            font-size: .26rem;
            line-height: .9rem;
            text-align: center;
            color: #fff;
            overflow: hidden;
            border-radius: .03rem;
            background-color: #f14d4c;
        }
        .h50{
            height: .5rem;
        }
    }
</style>



