<template>
  <div class="chartWrap" v-if="rendered" :style="marketOption.style"><div ref="marketChart"></div></div>
</template>


<script>
    //  const KKE = require("http://finance.sina.com.cn/sinafinancesdk/js/sf_sdk.js");

    export default {
        name: 'market',
        data () {
            return {
                dom: this.$refs.marketChart,
                symbol : this.marketOption.symbol || '000001'
            }
        },
        props: {
            marketOption: ""
        },
        computed:{

        },
        created:function () {
            this.rendered = true;
        },
        mounted:function () {
            const url = "http://finance.sina.com.cn/sinafinancesdk/js/sf_sdk.js";
            this.createScript(url,this.initData)
        },
        methods:{
            createScript : function (url,callback) {
                var script = document.createElement("script");
                script.type = "text/javascript";
                script.callback = typeof callback != "undefined" ? callback : new Function();
                script[document.all ? "onreadystatechange" : "onload"] = function () {
                    if (document.all && this.readyState != "loaded" && this.readyState != "complete") {
                        return;
                    }
                    this.callback(this);
                    this.callback = null;
                    this[document.all ? "onreadystatechange" : "onload"] = null;
                    this.parentNode.removeChild(this);
                };
                script.src = url;
                document.getElementsByTagName("head")[0].appendChild(script);
            },
            initData : function () {
                let symbol = this.symbol;
                let dom = this.$refs.marketChart;
                let redRaise = "0";
                let isFund = true;
                let hasRedRaise = typeof redRaise !== 'undefined';
                let chartColor = ['#ff484a', '#61ca59'];

                if (hasRedRaise) {
                    var theme = {
                        //k candle:
                        K_RISE: chartColor[redRaise], //k 
                        K_FALL: chartColor[1 - redRaise], //k 

                        //tick:
                        K_MS_RISE: chartColor[redRaise],
                        K_MS_FALL: chartColor[1 - redRaise],

                        //t(scales) related:
                        T_RISE: chartColor[redRaise], // 
                        T_FALL: chartColor[1 - redRaise] // 
                    };
                }
                var param = {
                    wrap: {
                        dom: dom // dom,
                    },
                    chart: {
                        symbol: symbol,  // 
                        kInitParam: {
                            theme: theme
                        },
                        tInitParam: {
                            theme: theme
                        },
                        netWorthInitParam: {
                            theme: theme,
                            dim : {
                                posX:30
                            },
                            dual:{
                                theme:{
                                    K_CL:'#ffc574'
                                }
                            }
                        },
                        repayInitParam: {
                            theme: theme,
                            dim : {
                                posX:40
                            }
                        },
                        predictInitParam: {
                            theme: theme
                        },
                        isFund: !!isFund
                    },
                    info: hasRedRaise
                        ?
                        {
                            upColor: chartColor[redRaise],
                            downColor: chartColor[1 - redRaise]
                        }
                        : {}
                };

                this.merge(theme, {
                    BG: '#000', //
                    T_P: '#f14d4c', //
                    T_AVG: '#f8b551', //
                    T_PREV: '#9b9b9b', //

                    //k candle:
                    K_N: '#999', //k

                    //tick:
                    K_MS_N: '#000000',
                    //t(scales) related:
                    T_N: '#999', // 

                    K_EXT: '#999', //k
                    T_T: '#999', //
                    K_P: '#999', //k
                    V_SD: '#333', //
                    M_ARR: ['#fff', '#BCD4F9'], //mountain

                    TIME_S: '#333', //
                    TIME_L: '#f3f6f9', //
                    GRID: '#f3f6f9', //

                    IVH_LINE: '#999', //
                    P_TC: '#333', //
                    P_BG: '#fff', //
                    T_TC: '#333', //
                    T_BG: '#fff', // #3e3d44

                    REMARK_T: '#fff', //k
                    REMARK_BG: 'rgb(62, 61, 68)',
                    K_PCT: '#999', //k
                    BTN_ARR: ['#2e2d32', '#999'], //[,]
                    TIP_ARR: ['#2f2d32', '#999', null, true, '40%'], //[,]
                    LOGO: '#ccc',  //

                    K_CL:'#f14d4c'
                }, true);
                this.merge(param, {
                    wrap: {
                        style: {
                            backgroundColor: '#fff'
                        }
                    },
                    tech: {
                        boxStyle: {
                            backgroundColor: 'rgb(255, 255, 255)'
                        },
                        itemNormalStyle: {
                            color: '#666'
                        }
                    },
                    tab: {
                        style: {
                            backgroundColor: 'rgb(255, 255, 255)'
                        },
                        navStyle: {
                            backgroundColor: 'rgb(255, 255, 255)'
                        },
                        navItemStyle: {
                            color: '#666',
                            fontSize:'16px',
                            backgroundColor: 'rgb(255, 255, 255)'
                        },
                        navItemActiveStyle: {
                            color:'#f8b551',
                            backgroundColor: '#fff',
                            borderBottom:'3px solid #f8b551'
                        },
                        subNavItemStyle: {
                            color: '#666',
                            backgroundColor: 'rgb(255, 255, 255)'
                        },
                        subNavItemActiveStyle: {
                            color: '#ff484a',
                            backgroundColor: 'rgb(255, 255, 255)'
                        }
                    },
                    info: {
                        simpleStyle: {
                            color: '#666',
                            backgroundColor: '#fff'
                        },
                        completeStyle: {
                            color: '#666',
                            backgroundColor: 'rgb(255, 255, 255)'
                        },
                        nameStyle: {
                            color: '#666'
                        },
                        levelColor: 'white'
                    }
                }, true);

                this.draw(param);
            },
            draw : function (param) {
                KKE.api('plugins.sinaAppTKChart.get', param, function (chart_) {
//          window.chart = chart_;
                    chart_.setDirection('vertical');

                    window.addEventListener('resize', function () {
                        chart_.resize();
                    });

//          try {
//            SUDA.uaTrack('chart_detail', 'appchart');
//          } catch (e) {}
                });
            },
            isClass : function (obj_) {
                var toString = Object.prototype.toString;
                if (obj_ === null) return "Null";
                if (obj_ === undefined) return "Undefined";
                return toString.call(obj_).slice(8, -1);
            },
            merge : function (to_, from_, overwrite_) {
                if (!from_) return to_;
                if (!to_) to_ = {};

                for (var _prop in from_) {
                    if (from_.hasOwnProperty(_prop)) {
                        if (this.isClass(from_[_prop]) === 'Object') { // 
                            !to_[_prop] && (to_[_prop] = {});
                            this.merge(to_[_prop], from_[_prop], overwrite_);
                        } else if (overwrite_ || !(_prop in to_)) { // 
                            to_[_prop] = from_[_prop];
                        }
                    }
                }
                return to_;
            }
        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .chartWrap{
    width: 100%;
    height: 400px;
    padding:.2rem 0;
    background-color: #fff;
  }
</style>



