<template>
  <div class="orderWrap">
      <header>
        <a class="left-arr" href="/#"><i class="icon-back"></i></a>
        <p></p>
        <span class="right-txt"></span>
      </header>
      <div class="wrap">
        <div class="wrap-con">
          <div class="fund-type">
            <div class="names border-1px clearfix">
              <span>A</span>
              <span>(80880822)</span>
            </div>
            <div class="types clearfix">
              <span></span>
              <span><i>10</i></span>
            </div>
          </div>
          <div class="amount">
            <label></label>
            <input type="text" placeholder="10"/>
          </div>
          <div class="tips"> *0.8%=1.45</div>
          <div class="agreeBtn" @click="toPay"></div>
          <div class="agreement"><a href="#"></a><a href="#"></a></div>
        </div>
      </div>
  </div>
</template>
<script>
  export default{
      methods:{
          toPay:function () {
              this.$router.push({
                  path:'/pay/paysure',
              })
          }
      }
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @mixin border-1px($color){
    position:relative;
    &:after{
      display:block;
      position:absolute;
      left:0;
      bottom:0;
      width:100%;
      border-top:1px solid $color;
      content:'';
    }
  }
  @media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5){
    .border-1px{
      &:after{
        -webkit-transform: scaleY(0.7);
        transform: scaleY(0.7);
      }
    }
  }
  
  @media (-webkit-max-device-pixel-ratio: 2), (max-device-pixel-ratio: 2){
    .border-1px{
      &:after{
        -webkit-transform: scaleY(0.5);
        transform: scaleY(0.5);
      }
    }
  }
  .clearfix:after {
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
  }
  .clearfix{*zoom:1;}
  header{
      width: 100%;
      height: 1rem;
      margin: 0 auto;
      line-height: 1rem;
      display: -webkit-flex;
      display: -webkit-box;
      background: #fff;
      font-size: 0;
      box-shadow: 0 0 5px rgba(0,0,0,.2);
      position:relative;
      z-index: 99;
      .left-arr{
        display: inline-block;
        padding-left:0.2rem;
        height: 100%;
        width: 0.8rem;
        color: #5e5e5e;
        font-size: 20px;
        .icon-back{
          /*background: url(../../assets/icon-back.png) center 0 no-repeat;*/
          background-size: 0.26rem 0.47rem;
          width: 0.26rem;
          height: 0.47rem;
          display: inline-block;
          vertical-align: middle;
        }
      }
      p{
        font-size:0.36rem;
        color: #666;
        -webkit-flex: 1;
        -webkit-box-flex: 1;
        text-align: center;
        line-height: 1rem;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .right-txt{
        font-size: 0.26rem;
        margin-right: 0.2rem;
        color:#5781bd;
        font-weight: 700;
      }
    }
  .wrap{
    position: absolute;
    left: 0;
    top: 1rem;
    right: 0;
    bottom: 0;
    overflow: hidden;
    .wrap-con{
      overflow: hidden;
      overflow-y: auto;
      height: 100%;
      -webkit-overflow-scrolling: touch;
      .fund-type{
        background: #fff;
        margin: 0.2rem 0;
        .names{
          height: 0.66rem;
          line-height: 0.66rem;
          font-size:0.3rem;
          @include border-1px(rgb(238,238,238));
          &:after{
            visibility: visible;
          }
          span{
            &:first-child{
              float: left;
              padding-left: 0.2rem;
              color: #333;
            }
          }
          span{
            &:last-child{
              float: right;
              padding-right: 0.2rem;
              color: #f8b551;
            }
          }
        }
        .types{
          height: 0.9rem;
          line-height: 0.9rem;
          font-size:0.3rem;
          color: #999;
          span{
            &:first-child{
              float: left;
              padding-left: 0.2rem;
            }
          }
          span{
            &:last-child{
              float: right;
              padding-right: 0.2rem;
            }
            i{
              color: #f8b551;
              font-style: normal;
              padding: 0 5px;
            }
          }
        }
      }
      .amount{
        font-size: 0.3rem;
        height: 0.9rem;
        line-height: 0.9rem;
        background: #fff;
        padding: 0 0.2rem;
        input{
          height: 100%;
          line-height: 0.9rem;
          outline: none;
          border: none;
          padding-left: 0.16rem;
        }
      }
      .tips{
        padding: 0.3rem 0.2rem;
        font-size: 0.3rem;
        color: #999;
      }
      .agreeBtn{
        font-size: 0.3rem;
        height: 0.9rem;
        line-height: 0.9rem;
        text-align: center;
        margin-left: 0.2rem;
        margin-right: 0.2rem;
        background: #f14d4c;
        color: #fff;
        border-radius: 4px;
      }
      .agreement{
        padding:0.3rem 0 0 0.16rem;
        font-size: 0.28rem;
        a{
          color: #5781bd;
        }
      }
    }
  }
</style>



