<template>
    <div class="route-fade-in"  ref="comBodyBaoxian">
        <div>
            <div class="baoxian-star">
                <img src="../assets/bxbg.jpg" alt="">
            </div>
            <div class="baoxian-tabs">
                <div v-for="(item,index) in tabs"
                     @click="toggle(index,item.tabView)"
                     :class="{current:now===index}">{{item.tabContent}}
                </div>
            </div>
            <!---->
            <!---->
            <!--mode=out-in-->
            <transition name="slide-fade" mode="out-in">
                <keep-alive>
                    <component :is="currentView"></component>
                </keep-alive>
            </transition>
            <!--BS-->
            <div style="height: 1rem"></div>
        </div>
    </div>
</template>

<script>
    //    better scroller
    import BScroll from 'better-scroll';
    import BTabOne from '../components/Tabs/BaoxianTab01'
    import BTabTwo from '../components/Tabs/BaoxianTab02'
    import BTabThree from '../components/Tabs/BaoxianTab03'
    import VStar from '../components/Form/RateStar'
    export default {
        name: '',
        components: {
            BTabOne,
            BTabTwo,
            BTabThree,
            VStar
        },
        data () {
            return {
                BaoxianScroll:'',
                //nowclass
                now:0,
                //currentView
                currentView:'BTabOne',
                //
                tabs:[
                    {
                        tabContent:'',
                        tabView:'BTabOne'
                    },
                    {
                        tabContent:'',
                        tabView:'BTabTwo'
                    },
                    {
                        tabContent:'',
                        tabView:'BTabThree'
                    },
                ]
            }
        },
        methods:{
            clickLi:function () {
                this.$router.push({path:'/home/baoxiandetail'})
            },
            _BaoxianScroll() {
                this._BaoxianScroll = new BScroll(this.$refs.comBodyBaoxian,{
                    click: true,
                    deceleration: 0.005,
                });
            },
            toggle:function(i,v){
                //datathis
                this.currentView=v;
                this.now=i;
            }
        },
        created(){
            this.$nextTick(function(){

                this.$refs.comBodyBaoxian.style.height=this.$parent.$el.firstChild.offsetHeight+'px'
                setTimeout(()=>{
                    this._BaoxianScroll()
                },10)


            })

        },
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
    .baoxian-tabs{
        overflow: hidden;
        margin-bottom: .3rem;
        div{
            width: 1.6rem;
            height: .7rem;
            float: left;
            font-size: .3rem;
            text-align: center;
            &.current{
                color: #f8b651;
                border-bottom: 2px solid #f8b651;
            }
        }
    }
    .baoxian-star{
        height: 2rem;
        img{ height: 2rem}
    }
    .slide-fade-enter-active {
        transition: all .3s ease;
    }
    .slide-fade-leave-active {
        transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-active {
        transform: translateX(10px);
        opacity: 0;
    }
</style>



