<template>
    <div class="vercode">
        <header>
            <a class="arrow-back"></a>
            <span></span>
            <a class="r-href"></a>
        </header>
        <div class="form-tpd">
            <ul class="form">
                <li>
                    <span class="key">137*****333</span>
                </li>
            </ul>
            <ul class="form">
                <li>
                    <input type="text" placeholder="">
                    <div class="f-btn"></div>
                </li>
            </ul>
            <div class="btn" @click="toOrderSure"></div>
        </div>
    </div>
</template>

<script>
    export default {
        name: '',
        components: {},
        data () {
            return {}
        },
        methods:{
            toOrderSure:function () {
                this.$router.push({
                    path:'/order/ordersure',
                })
            }
        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
    .vercode{
        background: #f5f5f5;
        header{
            /*position: fixed;*/
            /*top: 0;*/
            position: relative;
            width: 100%;
            height: .9rem;
            font-size: .32rem;
            line-height: .9rem;
            text-align: center;
            color: #666;
            background-color: #fff;
            box-shadow: 0 .02rem .02rem 0 #e9ebed;
        }
        .arrow-back{
            position: absolute;
            margin: auto;
            left: .28rem;
            top: 0;
            bottom: 0;
            width: .3rem;
            height: .3rem;
            transform: rotate(45deg);
            border-left: 1px solid #cecece;
            border-bottom: 1px solid #cecece;
        }
        .r-href{
            position: absolute;
            right: .2rem;
            font-size: .26rem;
            color: #5781bd;
            text-decoration: none;
        }
        .verify .f-default{
            padding: .9rem 0 .16rem;
            font-size: 0;
            text-align: center;
        }
        .verify h3{
            font-size: .32rem;
            line-height: 1.5;
            text-align: center;
            color: #f8b551;
        }
        .verify .subtit{
            padding-top: .08rem;
            font-size: .26rem;
            line-height: 1.5;
            text-align: center;
            color: #999;
        }
        .verify .top-margin{
            margin-top: .94rem;
        }
        .form-tpd{
            padding-top: .82rem;
        }
        .form{
            margin-top: .2rem;
            background-color: #fff;
        }
        .form li{
            display: flex;
            margin-left: .2rem;
            height: .9rem;
            border-bottom: 1px solid #eee;
            overflow: hidden;
        }
        .form li:last-of-type{
            border-bottom: none;
        }
        .form .key{
            font-size: .26rem;
            line-height: .84rem;
            color: #666;
        }
        .form .value{
            flex-grow: 1;
            margin-right: .2rem;
            font-size: .26rem;
            line-height: .84rem;
            text-align: right;
            color: #f8b551;
        }
        .form label{
            align-self: center;
            flex-shrink: 0;
            margin-right: .38rem;
            padding-right: .4rem;
            height: .3rem;
            border-right: 1px solid #eee;
            font-size: .26rem;
            line-height: .3rem;
            color: #666;
        }
        .form input{
            flex-grow: 1;
            flex-shrink: 1;
            margin-right: .2rem;
            min-width: 2.2rem;
            border: none;
            outline: none;
            font-size: .26rem;
        }
        .form input::-webkit-input-placeholder{
            color: #ccc;
        }
        .form .f-btn{
            width: 2rem;
            font-size: .26rem;
            line-height: .9rem;
            text-align: center;
            color: #fff;
            background-color: #5781bd;
        }
        .form .bank{
            flex-grow: 1;
            padding: 0 .1rem;
            margin-left: -.1rem;
            margin-right: .2rem;
            border: none;
            outline: none;
            font-size: .26rem;
            line-height: .9rem;
            text-overflow: ellipsis;
            color: #666;
            -webkit-appearance: none;
            -moz-appearance: none;
        }

        .btn{
            margin-top: .4rem;
            margin-left: .2rem;
            margin-right: .2rem;
            height: .9rem;
            font-size: .26rem;
            line-height: .9rem;
            text-align: center;
            color: #fff;
            overflow: hidden;
            border-radius: .03rem;
            background-color: #f14d4c;
        }
    }
</style>



