<template>
  <div class="canvasWrap">
    <div class="box" v-if="rendered">
      <canvas ref="myCanvas" style="width: 100%;height: 100%;"></canvas>
    </div>
    <div class="info">
      <div class="name" :style="{color:arcColor}">90</div>
      <div class="lab-group" v-if="openLab">
        <div class="cell" v-for="(item,index) in options.lab">
          <code :style="{color:arcColor,borderColor:arcColor}">{{item.name}}</code>
          <span>{{item.grade}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        data () {
            return {
                rendered: false,
                value: 90,
                isAnimate: true,
                bgColor: "#ddd",
                arcColor: "#f8b551",
                txtColor: "#ccc",
                openScale: true
            }
        },
        props: {
            options: ""
        },
        computed: {},
        methods: {
            setfill : function () {
                const self = this;
                const canvas = self.$refs.myCanvas;
                const width = canvas.clientWidth * 2;
                const height = canvas.clientHeight * 2;
                const base = height < width ? height : width;
                const option = {
                    ctxWidth : width,                //
                    ctxHeight : height,              //
                    radius : base * .35,				     //
                    lineWidth : base * .04,			     //
                    circleX : width * .5,            //X
                    circleY : base * .6,             //Y
                };
                var ctx = null;
                ctx = canvas.getContext("2d");
                ctx.canvas.width = width;
                ctx.canvas.height = height;

                if (this.isAnimate) {
                    let stepNum = 0;
                    !function animate() {
                        if (stepNum <= self.value) {
                            self.__setTime = setTimeout(animate, 10);
                            self.fill(ctx, option ,stepNum);
                            stepNum++;
                        } else {
                            clearTimeout(self.__setTime);
                        }
                    }();
                } else {
                    self.fill(ctx, option ,self.value);
                }
            },
            fill : function (ctx,option,num) {
                this.clearFill(ctx,option);
                if (this.openScale) {
                    this.fillScale(ctx,option);
                }
                this.fillBg(ctx,option);
                this.fillArc(ctx, option, num);
            },
            clearFill : function (ctx,option) {
                ctx.clearRect(0, 0, option.ctxWidth, option.ctxHeight);
            },
            fillBg : function (ctx,option) {
                const {circleX, circleY, radius,lineWidth} = option;
                const plan = 100;	                            //
                const startAngle = Math.PI / 180 * 135;				            //
                const endAngle = Math.PI / 180 * (135 + (2.7 * plan));		//

                ctx.beginPath();
                ctx.lineWidth = lineWidth;
                ctx.strokeStyle = this.bgColor;
                ctx.arc(circleX, circleY, radius, startAngle, endAngle, false);
                ctx.lineCap = "round";
                ctx.stroke();
            },
            fillArc : function (ctx, option, num) {
                const {circleX, circleY, radius,lineWidth} = option;
                const plan = num ? num : 100;	                        //
                const startAngle = Math.PI / 180 * 135;				            //
                const endAngle = Math.PI / 180 * (135 + (2.7 * plan));		//

                ctx.beginPath();
                ctx.lineWidth = lineWidth;
                ctx.strokeStyle = this.arcColor;
                ctx.arc(circleX, circleY, radius, startAngle, endAngle, false);
                ctx.lineCap = "round";
                ctx.stroke();
            },
            fillScale : function (ctx,option) {
                const scaleArr = ["0", "20", "50", "80", "100"];
                for (var key in scaleArr) {
                    this.fillScaleDraw(ctx,option, scaleArr[key]);
                }
            },
            fillScaleDraw : function (ctx,option, scale) {
                const {ctxWidth,circleX, circleY, radius,lineWidth} = option;
                const dx = lineWidth * 2; // 
                const dy = 2;  // 
                const deg = Math.PI / 180 * (135 + (2.7 * scale)); // 
                const offsetY = Math.sin(deg) * radius; // Y
                const offsetX = Math.cos(deg) * radius; // X
                const txtOffsetX = scale == 50 ? -15 : scale < 50 ? offsetX * .6 : offsetX * .3;
                const txtOffsetY = scale == 50 ? offsetY * .3 : offsetY * .1;

                ctx.save();
                ctx.font = ctxWidth / 750 * 28 + "px Microsoft YaHei ";
                ctx.fillStyle = this.txtColor;
                ctx.translate(circleX + offsetX, circleY + offsetY); // 
                ctx.fillText(scale, txtOffsetX, txtOffsetY);
                ctx.rotate(deg); // 
                ctx.fillRect(0, 0, dx, dy); // 

                ctx.restore();
            }
        },
        mounted: function () {
            this.setfill();
        },
        created() {
            this.rendered = true;
            for (var key in this.options) {
                this[key] = this.options[key];
            }
        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .canvasWrap {
    position: relative;
    width: 100%;
    height: 4rem;
    margin: 0 auto;
    overflow: hidden;
    background-color: #fff;

    .box {
      width: 100%;
      height: 100%;
    }

    .info {
      position: absolute;
      top: 55%;
      left: 0;
      width: 100%;
      z-index: 10;
      text-align: center;
      -webkit-transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      transform: translateY(-50%);

      .name {
        color: #f8b551;
        font-size: .48rem;
      }
      .lab-group {
        margin-top: .1rem;
        .cell {
          display: inline-block;
          margin: 0 .04rem;
          min-width: .62rem;
          code {
            font-style: normal;
            color: #f8b551;
            border: 1px solid #f8b551;
            font-size: .2rem;
            display: block;
            border-radius: .03rem;
          }
          span {
            display: block;
            line-height: .4rem;
            font-size: .28rem;
            color: #99c;
          }
        }
      }
    }
  }


</style>



