<template>
    <div class="page-view">
        <div class="com-body" ref="comBody">
            <!--better-scrollerrefdiv-->
            <div>
                <!---->
                <div class="zc">
                    <p class="zc-title"></p>
                    <p class="zc-num">0.00</p>
                    <p> 0.00 </p>
                </div>
                <!---->
                <div class="counts">
                    <div class="count-block count01">
                        <div class="count-head"></div>
                        <div class="count-border"><span></span></div>
                    </div>
                    <div class="count-block count02">
                        <div class="count-head"></div>
                        <div class="count-border"><span>0.00</span></div>
                    </div>
                    <div class="count-block count03">
                        <div class="count-head"></div>
                        <div class="count-border"><span>0.00</span></div>
                    </div>
                </div>
                <div class="jj">
                    <div class="jj-left"></div>
                    <div class="jj-right"></div>
                </div>
                <ul class="jj-ul">
                    <li class="jj-li" v-for="(item,index) in listData" @click="clickLi(index)">
                        <div class="jj-li-left">
                            <p class="jj-num">{{item.year_incratio}}</p>
                            <p></p>
                        </div>
                        <div class="jj-li-right">
                            <p class="jj-title">{{item.fund_name}}</p>
                            <p>{{item.tuijian}}</p>
                        </div>
                    </li>
                </ul>
                <div class="z-more" @click="lookMore"></div>
            </div>
        </div>
        <v-footer></v-footer>
    </div>
</template>

<script>
//    better scroller
    import BScroll from 'better-scroll';
//    
    import VFooter from '../components/layout/FooterLayout';
    export default {
        name: '',
//        
        components: {
            VFooter,
        },
        data () {
            return {

                listData:[],
                queryId:'',
                comBodyScroll:''

            }
        },
        created(){
//            this.axios.get('http://localhost:8080/static/IndexList.json')
//            this.axios.get(API_ROOT+'/static/IndexList.json')
            this.axios.get('http://172.16.59.143:8089/api/productfund/index?type=A')
                .then((response) => {

//                console.log(response.data.data)

                    this.listData=response.data.data;
                    //$nextTickdom
                    this.$nextTick(() => {
                        //
                        setTimeout(()=>{
                            this.indexScroll();
                        },300)
                    })
                })
                .catch((error) => {
                    console.log('error')
                });
        },
        methods:{
            clickLi:function (i) {

                this.$router.push({
                    path:'/home/detail',
                    query:{listId:this.listData[i].id}

                })
            },
            indexScroll() {
                this.comBodyScroll = new BScroll(this.$refs.comBody,{
                    click: true,
                    deceleration: 0.003,
                });
            },
            lookMore:function () {
                this.$router.push({
                    path:'/licai/jijin',
                })
            }
        }





    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
    .page-view{
        height: 100%;
        display: flex;
        flex-direction: column;
        .com-body{
            flex: 1;
            overflow-y: auto;
            .jj{
                padding: 0 0.2rem;
                margin: 1rem 0 .7rem;
                overflow: hidden;
                .jj-left{
                    float: left;
                    color: #333;
                    font-size: 0.3rem;
                }
                .jj-right{
                    float: right;
                    text-align: right;
                    font-size: .24rem;
                    color: #999;
                }
            }
            .jj-ul{
                .jj-li{
                    overflow: hidden;
                    padding: 0 0.9rem;
                    margin-bottom: 0.8rem;
                    font-size: .26rem;
                    color: #666;
                    .jj-li-left{
                        float: left;
                        .jj-num{
                            color: #f44e51;
                            font-size: 0.6rem;
                            height: 0.8rem;
                        }
                    }
                    .jj-li-right{
                        .jj-title{
                            color: #333;
                            font-size: 0.4rem;
                            height: 0.8rem;
                            padding-top: .1rem;
                            width: 3.2rem;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                        }
                        float: right;
                    }
                }
            }
        }
        .zc{
            text-align: center;
            font-size: .26rem;
            color: #666;
            padding: 1rem 0 .7rem;
            .zc-num{
                color: #f44e51;
                font-size: 0.8rem;
            }
        }
        .counts{
            display: flex;
            justify-content: space-between;
            padding: 0 .2rem;
            /*background: #fff;*/
            .count-block{
                width: 2.3rem;
                height: .9rem;
                overflow: hidden;
                .count-head{
                    height: .3rem;
                    border-top-left-radius: 0.1rem;
                    border-top-right-radius: 0.1rem;
                }
                .count-border{
                    height: .26rem;
                    line-height: .26rem;
                    margin: .3rem 0 0 .1rem;
                    padding-left: .1rem;
                    font-size: .26rem;
                    span{
                        margin-left: .2rem;
                    }
                }
            }
            .count01{
                .count-head{
                    background: #f8b551;
                }
                .count-border{
                    border-left: .08rem solid #f8b551;
                    span{
                        color: #5781bd;
                    }
                }
            }
            .count02{
                .count-head{
                    background: #d2c19b;
                }
                .count-border{
                    border-left: .08rem solid #d2c19b;
                    span{
                        color: #f14d4c;
                    }
                }
            }
            .count03{
                .count-head{
                    background: #cbcbcb;
                }
                .count-border{
                    border-left: .08rem solid #cbcbcb;
                    span{
                        color: #f14d4c;
                    }
                }
            }
        }
        .z-more{
            color: #5087c0;
            text-align: center;
            margin-bottom: .6rem;
            font-size: .28rem;
        }
        .z-more-bottom{
            padding-bottom: 1.4rem;
        }
    }
</style>



